      <th>–ù–æ–º–µ—Ä</th>
      <th>–ì–æ—Ä–æ–¥</th>
      <th>–î–æ–ª–∂–Ω–æ—Å—Ç—å</th>
    </tr>
    <tr v-for="employee in employees" :key="employee.value">
